import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import Carousel from 'react-material-ui-carousel';
import Stack from '@mui/material/Stack';
import ReviewItem from './ReviewItem';

export default function MemoryReviewCarousel({ memories, handleIndexChange, picIndex }) {
  const [carouselItems, setCarouselItems] = useState(memories);

  useEffect(() => {
    setCarouselItems(memories);
  }, [memories]);

  console.log('carousel items', carouselItems, picIndex);
  const getThumbnailSrcSet = (thumbnailImage) => {
    const srcSetList = thumbnailImage.images ? thumbnailImage.images.map(({ src, width }) => `${src} ${width}w`) : [];
    return srcSetList.join(',');
  };

  return (
    <Carousel
      sx={{
        maxWidth: '700px',
        width: '95%',
      }}
      indicators={false}
      index={picIndex}
      onChange={(ind) => handleIndexChange(ind)}
      navButtonsAlwaysVisible
      autoPlay={false}
    >
      {carouselItems.map((mem) => (
        <Stack
          key={mem.thumbnail.memory_id}
          direction="column"
          spacing={1}
          alignItems="center"
        >
          <ReviewItem
            title={mem.thumbnail.title}
            date={mem.thumbnail.date}
            thumbnailImage={mem.thumbnail.photo.src}
            thumbnailImageSrcSet={getThumbnailSrcSet(mem.thumbnail.photo)}
            handleImageLoad={() => {}}
            handleClick={() => {}}
          />
        </Stack>
      ))}
    </Carousel>

  );
}

MemoryReviewCarousel.propTypes = {
  picIndex: PropTypes.number.isRequired,
  handleIndexChange: PropTypes.func.isRequired,
  memories: PropTypes.arrayOf(PropTypes.shape({
    thumbnail: PropTypes.shape({
      memory_id: PropTypes.number.isRequired,
      title: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      photo: PropTypes.arrayOf(PropTypes.shape({
        key: PropTypes.string,
        src: PropTypes.string,
        title: PropTypes.string,
        alt: PropTypes.string,
        width: PropTypes.number,
        height: PropTypes.number,
      })),
    }),
    owner_details: PropTypes.shape({
      name: PropTypes.string.isRequired,
      email: PropTypes.string.isRequired,
      picture: PropTypes.string.isRequired,
    }),
  })).isRequired,
};
