import { useNavigate } from 'react-router-dom';
import useMediaQuery from '@mui/material/useMediaQuery';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';

import MemoryThumbnailCard from '../MemoryThumbnailCard';

export default function CreateMemoryItem() {
  const navigate = useNavigate();
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));

  const handleClick = () => {
    navigate('/memory/create');
  };

  return (
    <Grid
      item
      key="creatememory"
      lg={3}
      md={4}
      xs={6}
      sx={{ p: 1 }}
    >
      <MemoryThumbnailCard handleClick={handleClick}>
        <Typography
          variant={belowmd ? 'body1' : 'h5'}
          color="button.main"
          sx={{
            p: 1,
            color: 'secondary.main',
            bgcolor: 'primary.main',
            borderRadius: '0px 10px 10px 0px',
            borderColor: 'secondary.main',
            position: 'absolute',
            bottom: 15,
            left: 0,
          }}
        >
          Create a memory
        </Typography>
        <AddOutlinedIcon
          sx={{
            position: 'absolute',
            border: 3,
            borderRadius: '50%',
            fontSize: '7.5rem',
            left: '50%',
            top: '50%',
            transform: 'translate(-50%, -50%)',
            color: 'muted.main',
          }}
        />
      </MemoryThumbnailCard>
    </Grid>
  );
}
