import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import { useParams, useSearchParams } from 'react-router-dom';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Stack from '@mui/material/Stack';
import Badge from '@mui/material/Badge';

import BasePage from '../base_page';
import OwnMemories from './Own';
import SharedMemories from './Shared';
import ReviewMemories from './Review';

function MemoryModes({ mode, handleChange }) {
  return (
    <Stack direction="row" justifyContent="center" sx={{ py: 1, backgroundColor: 'button.main' }}>
      <ToggleButtonGroup
        color="primary"
        value={mode}
        exclusive
        aria-label="Memory Modes"
      >
        <ToggleButton value="own" sx={{ borderRadius: '30px' }} onClick={handleChange}>
          My Memories
        </ToggleButton>
        <ToggleButton value="shared" onClick={handleChange}>
          Shared With Me
        </ToggleButton>
        <ToggleButton value="review" sx={{ borderRadius: '30px' }} onClick={handleChange}>
          <Badge badgeContent={99} color="primary">
            Review
          </Badge>
        </ToggleButton>
      </ToggleButtonGroup>
    </Stack>
  );
}

function MemoryModeContent({ mode, memoryID }) {
  if (mode === 'own') {
    return <OwnMemories />;
  }

  if (mode === 'shared') {
    return <SharedMemories />;
  }

  if (mode === 'review') {
    return <ReviewMemories memoryID={memoryID} />;
  }
}

export default function Memories({ initMode }) {
  const params = useParams();
  const { memoryMode } = params;
  const [searchParams] = useSearchParams();
  const [mode, setMode] = useState(initMode);

  useEffect(() => {
    if (memoryMode) {
      setMode(memoryMode);
    } else {
      setMode(initMode);
    }
  }, [memoryMode, initMode]);

  console.log(initMode, memoryMode, params, searchParams);

  const handleChange = (event, newMode) => {
    setMode(newMode);
  };

  return (
    <BasePage>
      <Stack direction="column">
        <MemoryModes mode={mode} handleChange={handleChange} />
        <MemoryModeContent mode={mode} memoryID={searchParams.get('memoryID')} />
      </Stack>
    </BasePage>
  );
}

Memories.propTypes = {
  initMode: PropTypes.string.isRequired,
};

MemoryModes.propTypes = {
  mode: PropTypes.string.isRequired,
  handleChange: PropTypes.func.isRequired,
};

MemoryModeContent.defaultProps = {
  memoryID: '',
};

MemoryModeContent.propTypes = {
  mode: PropTypes.string.isRequired,
  memoryID: PropTypes.string,
};
