import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Skeleton from '@mui/material/Skeleton';
import GridView from './GridView';
import imageLoading from '../../static/images/image-loading.gif';
import MemoryThumbnail from '../MemoryThumbnail';
import api from '../Api';

async function getAcceptedMemories(
  handleMemoryCallback,
  handleTokenCallback,
  handleThumbnailCallback,
  nextToken = '',
) {
  try {
    const resp = await api.getAcceptedMemories(nextToken);
    if (resp) {
      if (resp.memories) {
        handleThumbnailCallback(resp.memories);
        const thumbnails = await Promise.all(
          api.getAcceptedThumbnails(resp.memories),
        );
        handleMemoryCallback(thumbnails);
        handleTokenCallback(resp.next_continuation_token || '');
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function SharedMemories() {
  const [nextToken, setNextToken] = useState('');
  const [memoryIDs, setMemoryIDs] = useState([]);
  const [memories, setMemories] = useState([]);
  const [count, setCount] = useState(0);
  const [grid, setGrid] = useState(null);
  const navigate = useNavigate();

  const handleClick = (memDetails) => {
    navigate(`/memory/shared/${memDetails.memory_id}`, { state: memDetails });
  };

  const picLoaded = () => {
    setCount((cnt) => cnt + 1);
  };
  const handleTokenChange = (newToken) => setNextToken(newToken);

  const addMemories = (details) => {
    details.map((detail) => {
      const memDetails = detail.thumbnail;
      memDetails.owner = detail.owner_details;
      console.log('combined shared memory details', memDetails);
      setMemories((arr) => arr.concat([memDetails]));
      return 0;
    });
    //  setMemories((arr) => arr.concat(details));
  };

  const addMemoryIDs = (ids) => {
    setMemoryIDs((arr) => arr.concat(ids));
  };

  useEffect(() => {
    getAcceptedMemories(
      addMemories,
      handleTokenChange,
      addMemoryIDs,
      nextToken,
    );
  }, []);

  function GetThumbnailGrid() {
    const thumbnailGrid = (
      <GridView
        create={false}
        memories={memories}
        nextToken={nextToken}
        handleMoreClick={() => getAcceptedMemories(
          addMemories,
          handleTokenChange,
          addMemoryIDs,
          nextToken,
        )}
        handleImageLoad={picLoaded}
        handleClick={handleClick}
      />
    );
    setGrid(thumbnailGrid);
  }

  useEffect(() => GetThumbnailGrid(), [memories, nextToken]);

  return (memories.length === memoryIDs.length) && (count >= memoryIDs.length) ? (
    grid
  ) : (
    <>
      <Grid
        container
        sx={{ bgcolor: 'button.main', py: 1 }}
        justifyContent="left"
        alignItems="flex-start"
      >

        {memoryIDs.map((memory) => (
          <Grid item key={memory} lg={3} md={4} xs={6} sx={{ p: 1 }}>
            <MemoryThumbnail
              title={<Skeleton type="rectangular" sx={{ width: '60%' }} />}
              date={<Skeleton type="rectangular" sx={{ width: '50%' }} />}
              thumbnailImage={imageLoading}
              handleClick={() => {}}
            />
          </Grid>
        ))}
      </Grid>

      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </>
  );
}
