import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import Avatar from '@mui/material/Avatar';
import MemoryThumbnail from '../MemoryThumbnail';

import CreateMemoryItem from './CreateMemoryItem';
import LoadMore from './LoadMore';

export default function GridView({
  memories, handleMoreClick, nextToken, handleImageLoad, create, handleClick,
}) {
  const sortedMemories = memories.sort((a, b) => (a.memory_id > b.memory_id ? 1 : -1));

  const getThumbnailSrcSet = (thumbnailImage) => {
    const srcSetList = thumbnailImage.images ? thumbnailImage.images.map(({ src, width }) => `${src} ${width}w`) : [];
    return srcSetList.join(',');
  };

  return (
    <Grid container sx={{ bgcolor: 'button.main', py: 1 }} justifyContent="left" alignItems="flex-start">
      {create ? <CreateMemoryItem /> : null}
      {sortedMemories.map((memory) => (
        <Grid
          item
          key={memory.title}
          lg={3}
          md={4}
          sm={6}
          xs={12}
          sx={{ p: 1 }}
        >
          <MemoryThumbnail
            title={memory.title}
            date={memory.date}
            avatar={memory.owner ? (
              <Avatar
                alt={memory.owner.name}
                src={memory.owner.picture}
                sx={{ referrerPolicy: 'no-referrer' }}
              >
                {Array.from(memory.owner.email)[0].toUpperCase()}
              </Avatar>
            ) : null}
            thumbnailImage={memory.photo.src}
            thumbnailImageSrcSet={getThumbnailSrcSet(memory.photo)}
            handleClick={() => handleClick(memory)}
            handleImageLoad={handleImageLoad}
          />
        </Grid>
      ))}
      { nextToken && (<LoadMore handleMoreClick={handleMoreClick} />) }
    </Grid>
  );
}

GridView.defaultProps = {
  memories: [],
};

GridView.propTypes = {
  create: PropTypes.bool.isRequired,
  memories: PropTypes.arrayOf(
    PropTypes.shape({
      memory_id: PropTypes.number.isRequired,
      title: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      photo: PropTypes.arrayOf(PropTypes.shape({
        key: PropTypes.string,
        src: PropTypes.string,
        title: PropTypes.string,
        alt: PropTypes.string,
        width: PropTypes.number,
        height: PropTypes.number,
      })),
    }),
  ),
  nextToken: PropTypes.string.isRequired,
  handleMoreClick: PropTypes.func.isRequired,
  handleImageLoad: PropTypes.func.isRequired,
  handleClick: PropTypes.func.isRequired,
};
